import Vue from 'vue'
import Vuex from 'vuex'
import VueRouter from 'vue-router'
import axios from 'axios'
import App from './App.vue'
import TheSignup from './pages/TheSignup.vue'
import TheItemAdd from './pages/TheItemAdd.vue'
import TheItemList from './pages/TheItemList.vue'
import './assets/css/bootstrap.min.css'
import './assets/js/jquery-3.3.1.js'
import './assets/js/bootstrap.min.js'
Vue.config.productionTip = false;
//vuerouter
Vue.use(VueRouter);
axios.defaults.baseURL = 'http://8.129.133.156:8080/NotebookSPA0/';
Vue.prototype.$axios = axios
// 2. 
//  "component" 
//  Vue.extend() 
// 
// 
const routes = [{
		path: '/',
		redirect: '/user_signup'
	},
	{
		path: '/user_signup',
		component: TheSignup,
		meta:{title:""},
	},
	{
		name:"item_list",
		path: '/item_list',
		component: TheItemList,
		meta:{title:""},
	},
	{
		path: '/item_add',
		component: TheItemAdd,
		meta:{title:""},
	}
];

// 3.  router  `routes` 
// , 
const router = new VueRouter({
	mode: 'history',
	routes:routes, // ()  routes: routes
});

router.beforeEach((to,from,next)=>{
	if(to.meta.title){
		document.title = to.meta.title;
	}
	next();
});

var vm = new Vue({
	data:{},
	methods:{},
	computed:{},
});

Vue.use(Vuex);
const store = new Vuex.Store({
  state: {
	user:{
		id:0,
		username:"",
	}
  },
  getters:{
	  getUser:function(){
		return state.user.id + "-" +state.user.username; 
	  },
  },
  mutations: {    
	setUser:function(state,user){
		state.user.id = user.id;
		state.user.username = user.username;
	},	
  }
})
//Vue.prototype.$store = store;
// 4. 
//  router 
// 
new Vue({
	router:router,
	store:store,
	render: h => h(App),
}).$mount('#app')
